import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
import Card from '@/components/common/card/card.vue'
import Params from '@/components/common/params/params.vue'
import Price from '@/components/common/price/price.vue'
import List from '@/components/search/list/list.vue'
export default {
	data() {
		return {
			// 导航栏数据
			screen: {
				currentIndex : 0,
				list: [
					// 状态1代表升序，状态2代表降序
					{name: '综合',status: 1},
					{name: '销量',status: 0},
					{name: '价格',status: 0}
				]
			},
			// 抽屉数据
			drawerShow: false,
			// 分类数据
			service: {
				type: 'service',
				selectedIndex: 0,
				list: [
					{name: '促销'},
					{name: '分期'},
					{name: '仅看有货'}
				],
			},
			classify: {
				type: 'classify',
				selectedIndex: 0,
				list: [
					{name: '耳机'},
					{name: '户外'},
					{name: '配件'}
				]
			},
			// 搜索列表数据
			productList: [
				{
					title: '真无线蓝牙耳机',
					topic: '/static/images/demo/demo6.jpg',
					des: '雅致简约/分体式入耳/收纳盒充电/蓝牙5.0/触控操作',
					price: 100,
					count: 1222,
					percent: '97%'
				},
				{
					title: '真无线蓝牙耳机',
					topic: '/static/images/demo/demo6.jpg',
					des: '雅致简约/分体式入耳/收纳盒充电/蓝牙5.0/触控操作',
					price: 100,
					count: 1222,
					percent: '97%'
				},
				{
					title: '真无线蓝牙耳机',
					topic: '/static/images/demo/demo6.jpg',
					des: '雅致简约/分体式入耳/收纳盒充电/蓝牙5.0/触控操作',
					price: 100,
					count: 1222,
					percent: '97%'
				},
				{
					title: '真无线蓝牙耳机',
					topic: '/static/images/demo/demo6.jpg',
					des: '雅致简约/分体式入耳/收纳盒充电/蓝牙5.0/触控操作',
					price: 100,
					count: 1222,
					percent: '97%'
				}
			],
			
			
		}
	},
	components: {
		uniDrawer,
		Card,
		Params,
		Price,
		List
	},
	methods: {
		// tab切换
		changeStatus(index) {
			if(index != this.screen.currentIndex) {
				for(let i = 0;i < this.screen.list.length;i++) {
					this.screen.list[i].status = 0
				}
				this.screen.list[index].status = 1;
				this.screen.currentIndex = index
			}
			else {
				this.screen.list[index].status=this.screen.list[index].status == 1?2:1
			}
		},
		// 监听选择侧滑栏的商品属性
		changeIndex(payload) {
			let type = payload.type;
			if(type == 'service') {
				this.service.selectedIndex = payload.index
			}
			else if(type == 'classify') {
				this.classify.selectedIndex = payload.index
			}
			
		}
	}
}
